import { useEffect } from "react";
import srv from "../fetch_";
function Login({ settoken }) {
  useEffect(()=>{
    //check is login
    srv.login({checkname:"true"},(data)=>{
      console.log(data)
      if (data.success) {
        settoken(data.name);
      }
    })
  },[])
  /////////////////////////////////////////////////////
  
  function login(evt) {
    evt.preventDefault();
    let name = evt.currentTarget.querySelector("input").value;

    srv.login({name}, (data) => {
      if (data.success) {
        settoken(data.name);
      }
    })
  }
  /////////////////////////////////////////////////////
  return (
    <div>
      <link rel="stylesheet" href="https://unpkg.com/spectre.css/dist/spectre.min.css" />
      <link rel="stylesheet" href="https://unpkg.com/spectre.css/dist/spectre-exp.min.css" />
      <link rel="stylesheet" href="https://unpkg.com/spectre.css/dist/spectre-icons.min.css" />
      <div className="hero bg-gray">
        <div className="hero-body">

          <div className="columns">
            <div className="col-6 col-mx-auto">

              <div className=" p-2">
                <div className="panel-header">
                  <div className="panel-title">
                    <p>Tuner App</p>
                    <div className="parallax" style={{ maxHeight: "50%" }}>
                      <div className="parallax-top-left" tabIndex="1"></div>
                      <div className="parallax-top-right" tabIndex="2"></div>
                      <div className="parallax-bottom-left" tabIndex="3"></div>
                      <div className="parallax-bottom-right" tabIndex="4"></div>
                      <div className="parallax-content" style={{ borderRadius: "5%", border: "10px solid black" }}>
                        <div className="parallax-front">
                          <h2 className="text-dark">parallax demo</h2>
                        </div>
                        <div className="parallax-back" style={{ backgroundColor: "black" }}>
                          <img src='data:image/gif;base64,'
                            className="img-responsive rounded" alt="gif pic" style={{ margin: "auto", maxHeight: "25vh" }} />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="panel-nav">
                  <div className="divider"></div>

                  <h3 className="text-center">[may I have you Name]</h3>
                  <div className="divider"></div>
                </div>
                <form onSubmit={login}>
                  <div className="panel-body">

                    <div className="form-group has-icon-left tooltip my-2" data-tooltip="all knowledge is memory">
                      <label className="form-label" htmlFor="input-name-1"></label>
                      <input className="form-input " type="text" id="input-name-1" placeholder="Name" />
                    </div>
                  </div>
                  <div className="panel-footer">
                    <div className="divider"></div>
                    <div className="columns">
                      <div className="col col-ml-auto">
                        <button className="btn">Submit</button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>

            </div>

          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;
